type Company = {
  companyName: string;
  companyEmail: string;
  companyKey: string;
  numEmployees: number;
  ABN: number;
};

type User = {
  email: string;
  password: string;
  name: string;
  companyName: string;
  userId: string;
  tokens: Array<string>;
};

// temp
type Invoice = {
  invoiceId: string;
  invoiceXML: string;
  user: object;
};

// temp
type Schema = {
  users: User[];
  invoices: Invoice[];
  companies: Company[];
};

let data: Schema = {
  users: [],
  invoices: [],
  companies: [],
};

function getData() {
  return data;
}

function setData(newData: any) {
  data = newData;
}

export { getData, setData, Schema };
